                :key="i">
                <v-row align="center" justify="center">
                    <v-col cols="4">
                        <v-row align="center" justify="center">
                            <v-img :src="item.icon" max-width="80"></v-img>
                        </v-row>
                    </v-col>
                    <v-col cols="8">
                        <h4 class="content-subtitle">{{item.title}}</h4>
                        <span class="card-subtitle">{{item.subtitle}}</span>
                    </v-col>
                </v-row>
            </v-col>
        </v-row>
    </v-container>
    
</template>

<script>
export default {
    name: 'pContent',
    data: () => ({
        contentItems: [
            {
                icon: require("../assets/speedometer.png"),
                title: "Скорость",
                subtitle: "Быстрые решения и реализация требований"
            },
            {
                icon: require("../assets/quality.png"),
                title: "Качество",
                subtitle: "Лучшая оптимизация и скорость приложений",
            },
            {
                icon: require("../assets/help.png"),
                title: "Поддержка",
                subtitle: "Готовность всегда оказать помощь в решении технических проблем"
            }
        ]
    }),
    computed: {
        isMobile () {
            switch (this.$vuetify.breakpoint.name) {
                case 'xs': return true;
                case 'sm': return true; 
                default: return false;
            }
        },
