<template>
    <v-container class="p-header wrapper" fluid>
        <v-container>
            <v-row align="center">
                <v-col md="6" xs="12" sm="12" lg="6">
                    <v-row>
                        <h1 :class="isMobile ? 'mobile-title' : 'd-title'">Ваш лучший помощник и компаньон в бизнесе</h1>
                    </v-row>
                    <v-row>
                        <h2 :class="isMobile ? 'mobile-subtitle' : 'd-subtitle'">разработка программного обеспечения, мобильных и веб приложений</h2>
                    </v-row>
                    
                    <v-row align="center" :justify="isMobile ? 'center' : 'start'">
                        <v-dialog
                            v-model="dialogController"
                            persistent
                            max-width="300"
                            >
                                <template v-slot:activator="{ on, attrs }">
                                    <v-btn 
                                        elevation="2" 
                                        x-large 
                                        color="#f39c12" 
                                        dark 
                                        :width="isMobile ? '100%' : '40%'"
                                        v-bind="attrs"
                                        v-on="on">
                                        Написать нам
                                        <v-icon right dark>
                                            mdi-pencil
                                        </v-icon>
                                    </v-btn>
                                </template>
                            <v-card>
                                <v-card-title class="text-h5">
                                Почта для обращения
                                </v-card-title>
                                <v-card-text>
                                    <v-text-field v-model="email" ref="emailInput" readonly filled ></v-text-field>
                                </v-card-text>
                                <v-card-actions>
                                <v-spacer></v-spacer>
                                <v-btn
                                    color="green darken-1"
                                    text
                                    @click="dialogController = false"
                                >
                                    Отмена
                                </v-btn>
                                <v-btn
                                    color="green darken-1"
                                    text
                                    @click="copyMail"
                                >
                                    Копировать
                                </v-btn>
                                </v-card-actions>
                            </v-card>
                            </v-dialog>
                    </v-row>
                </v-col>
                <v-col md="6" xs="12" sm="12" lg="6">
                    <div class="img-wrapper">
                        <v-img 
                            :class="isMobile ? 'mobile-image' : 'd-image'"
                            src="../assets/header-image.png"/>
                    </div>
                </v-col>
            </v-row>
        </v-container>
        <v-snackbar
            v-model="snackbar"
            :timeout="timeout"
            >
            {{ snackbarText }}

            <template v-slot:action="{ attrs }">
                <v-btn
                color="#f39c12"
                text
                v-bind="attrs"
                @click="snackbar = false"
                >
                Закрыть
                </v-btn>
            </template>
        </v-snackbar>
    </v-container>
</template>

<script>
export default {
    name: 'PHeader',
    data: () => ({
        email: 'alex@itservice-net.ru',
        dialogController: false,
        snackbar: false,
        snackbarText: 'Почта скопирована',
        timeout: 2000,
    }),
    computed: {
        isMobile () {
            switch (this.$vuetify.breakpoint.name) {
                case 'xs': return true;
                case 'sm': return true; 
                default: return false;
            }
        },
    },
    methods: {
      onResize () {
        this.isMobile = window.innerWidth < 600
      },
      copyMail() {
        let textToCopy = this.$refs.emailInput.$el.querySelector('input')
        textToCopy.select()
        document.execCommand("copy");
        this.dialogController = false;
        this.snackbar = true;
      }
    },
}
</script>

<style scoped>
    .wrapper {
        position: relative;
        background: #f5f6fa;
    }
    .d-title {
        font-family: 'Ubuntu', sans-serif;
        font-size: 46px;
        color: #2d3436;
        padding: 0px;
        margin: 0px;
        line-height: 50px;
    }
    .mobile-title {
        font-family: 'Ubuntu', sans-serif;
        text-align: center;
        font-size: 28px;
        color: #2d3436;
    }
    .d-subtitle {
        font-family: 'Ubuntu', sans-serif;
        font-size: 16px;
        color: #636e72;
        line-height: 100px;
    }
    .mobile-subtitle {
        font-size: 16px;
        font-family: 'Ubuntu', sans-serif;
        text-align: center;
        margin: 1% 0 5% 0;
        color: #636e72;
    }
    .d-image {
         max-width: 600px;
    }
    .mobile-image {
        object-fit: inherit;
        max-width: 350px;
    }
    .img-wrapper {
        max-width: 100%; 
        max-height: 100%;
        display: flex;
        justify-content: center;
        align-items: center;
        overflow: hidden;
    }
    /* .p-header .info, .p-header .image  {
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;
    } */
</style>